@font-face {
  font-family: "TTLakes-Regular";
  font-style: normal;
  src: url("../fonts/TTLakes-Regular.woff2") format("woff2"), url("../fonts/TTLakes-Regular.woff") format("woff"); }

@font-face {
  font-family: "TTLakes-Medium";
  font-style: normal;
  src: url("../fonts/TTLakes-Medium.woff2") format("woff2"), url("../fonts/TTLakes-Medium.woff") format("woff"); }

@font-face {
  font-family: "TTLakes-Bold";
  font-style: normal;
  src: url("../fonts/TTLakes-Bold.woff2") format("woff2"), url("../fonts/TTLakes-Bold.woff") format("woff"); }

.action-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex; }

.action-menu__item {
  position: relative;
  align-self: center;
  padding-right: 50px;
  padding-left: 30px; }

.action-menu__item-link {
  text-decoration: none;
  color: #1b1c21; }
  .action-menu__item-link:hover, .action-menu__item-link:focus {
    color: #7e7e82;
    animation: color 0.1s ease-in-out; }
  .action-menu__item-link::before {
    position: absolute;
    content: "";
    top: -20px;
    right: 0;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    background-color: #ff3e79; }
  .action-menu__item-link:hover::before, .action-menu__item-link:focus::before {
    box-shadow: 0 0 2px 2px #ff3e79;
    animation: shadow 0.1s ease-in-out; }
  .action-menu__item-link--status::before {
    background-image: url("../img/icon-status.svg");
    background-size: 17px; }
  .action-menu__item-link--order::before {
    background-image: url("../img/icon-order.svg");
    background-size: 20px; }

@media (min-width: 768px) {
  .action-menu__item-link::before {
    top: -8px; } }

.action-menu__item-text {
  display: none; }

@media (min-width: 768px) {
  .action-menu__item-text {
    display: inline;
    color: inherit;
    font-family: "TTLakes-Medium", "Arial", sans-serif;
    font-size: 16px;
    line-height: 24px; } }

@keyframes shadow {
  from {
    box-shadow: 0 0 0 0 #ff3e79; }
  to {
    box-shadow: 0 0 2px 2px #ff3e79; } }

@keyframes color {
  from {
    color: #1b1c21; }
  to {
    color: #7e7e82; } }

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f8f8f8;
  margin-bottom: 4px;
  position: relative; }

@media (min-width: 768px) {
  .main {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start; } }

.main__desc {
  margin-right: 30px; }

.main__content {
  color: #1b1c21;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px; }

.main__image {
  background-image: url("../img/services-img.jpg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  min-height: 230px;
  align-items: center;
  margin: 0 -20px; }

@media (min-width: 768px) {
  .main__image {
    min-width: 360px;
    min-height: 260px;
    border-radius: 6px; } }

@media (min-width: 1440px) {
  .main__image {
    min-width: 532px;
    min-height: 307px; } }

.main__unroll {
  margin-left: 30px;
  position: relative;
  text-decoration: none;
  font-family: "TTLakes-Medium", "Arial", sans-serif;
  color: #1b1c21;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.2px; }
  .main__unroll:hover, .main__unroll:focus {
    color: #7e7e82;
    animation: color 0.1s ease-in-out; }
  .main__unroll::before {
    left: -20px;
    top: 2px;
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    border: 2px solid #b8ffec;
    border-left: none;
    border-top: none;
    transform: rotate(45deg); }
  .main__unroll::after {
    left: -20px;
    top: 8px;
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    border: 2px solid #b8ffec;
    border-left: none;
    border-top: none;
    transform: rotate(45deg); }

.nav-menu {
  list-style: none;
  padding: 0; }

.nav-menu__item {
  font-family: "TTLakes-Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #7e7e82; }

.nav-menu__item-link {
  padding: 8px 12px;
  text-decoration: none;
  color: inherit;
  display: block;
  border: 2px solid transparent;
  border-radius: 6px; }
  .nav-menu__item-link:hover, .nav-menu__item-link:focus {
    background-color: white;
    animation: hover 0.2s ease-out; }
  .nav-menu__item-link--active, .nav-menu__item-link:active {
    background-color: white;
    border: 2px solid #b8ffec; }

@keyframes hover {
  from {
    background-color: transparent; }
  to {
    background-color: white; } }

.services {
  margin: 0 auto;
  max-width: 1120px;
  font-family: "TTLakes-Regular", "Arial", sans-serif;
  background-color: #f8f8f8;
  padding: 42px 33px;
  box-sizing: border-box; }

.services__header {
  display: flex;
  justify-content: space-between; }

.services__headline {
  margin: 0;
  font-family: "TTLakes-Bold", "Arial", sans-serif;
  font-size: 28px;
  line-height: 40px;
  color: #1b1c21; }

.services__nav-menu {
  display: flex;
  flex-wrap: wrap; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }
